<!--  -->
<template>
  <div class="shelf-item" @click="onItemClick">
    <component
      :is="getItemType"
      :data="data"
      class="shelf-item-comp"
      :class="{ 'is-edit': isEditMode && data.type === 2 }"
    ></component>
    <div
      class="shelf-item-select-wrapper"
      v-show="isEditMode && data.type === 1"
      :class="{ 'shelf-item-selected': data.selected }"
    >
      <span class="icon-selected"></span>
    </div>
  </div>
</template>

<script>
import {
  gotoBookCategory,
  gotoBookDetail,
  gotoStoreHome,
} from '../../utils/store'
import ShelfItemAdd from './ShelfItemAdd'
import ShelfItemBook from './ShelfItemBook'
import ShelfItemCategory from './ShelfItemCategory'
import { storeShelfMixin } from '../../utils/mixin'

export default {
  mixins: [storeShelfMixin],
  components: {
    ShelfItemAdd,
    ShelfItemBook,
    ShelfItemCategory,
  },
  props: {
    data: Object,
  },
  data() {
    return {
      book: ShelfItemBook,
      category: ShelfItemCategory,
      add: ShelfItemAdd,
    }
  },
  computed: {
    getItemType() {
      return this.data.type === 1
        ? this.book
        : this.data.type === 2
        ? this.category
        : this.add
    },
  },

  methods: {
    onItemClick() {
      if (!this.isEditMode) {
        if (this.data.type === 1) {
          // console.log(1)
          gotoBookDetail(this, this.data)
        } else if (this.data.type === 2) {
          // console.log(this.data)
          gotoBookCategory(this, this.data.title)
        } else {
          // console.log(-1)
          gotoStoreHome(this)
        }
      } else {
        if (this.data.type === 1) {
          this.data.selected = !this.data.selected
          if (this.data.selected) {
            if (this.shelfSelected.indexOf(this.data) == -1) {
              this.shelfSelected.push(this.data)
            }
          } else {
            this.setShelfSelected(
              this.shelfSelected.filter((item) => {
                return item.id !== this.data.id
              })
            )
          }
        }
      }
    },
  },
}
</script>
