  position: relative;
  width: 100%;
  height: 100%;
  // z-index: 200;
  // background-color: royalblue;
  // height: px2rem(100);
  .shelf-item-comp {
    opacity: 1;
    &.is-edit {
      opacity: 0.5;
    }
  }
  .shelf-item-select-wrapper {
    position: absolute;
    right: px2rem(2);
    bottom: px2rem(2);
    color: #ccc;
    &.shelf-item-selected {
      color: blue;
    }
    .icon-selected {
      font-size: px2rem(18);
    }
  }
}
</style>
