<!--  -->
<template>
  <div class="shelf-footer-wrapper" v-show="isEditMode">
    <div class="shelf-footer-tab-wrapper">
      <div
        class="shelf-footer-tab"
        v-for="item in tabs"
        :key="item.index"
        :class="{ 'tab-show': opcityShow }"
        @click="chooseItem(item)"
      >
        <span
          class="icon-private icon-foot"
          v-if="item.index === 1 && !isPrivate"
        ></span>
        <span
          class="icon-private-see icon-foot"
          v-if="item.index === 1 && isPrivate"
        ></span>
        <span
          class="icon-download icon-foot"
          v-if="item.index === 2 && !isDownload"
        ></span>

        <span
          class="icon-download-remove icon-foot"
          v-if="item.index === 2 && isDownload"
        ></span>
        <span class="icon-move icon-foot" v-if="item.index === 3"></span>
        <span class="icon-shelf icon-foot" v-if="item.index === 4"></span>
        <span>{{ label(item) }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { removeLocalStorage, saveBookShelf } from '../../utils/localStorage'
import { storeShelfMixin } from '../../utils/mixin'
import { download } from '../../api/store'
import { removeLocalForage } from '../../utils/localForage'
export default {
  mixins: [storeShelfMixin],
  data() {
    return {}
  },
  computed: {
    isPrivate() {
      if (!this.isSelected) {
        return false
      } else {
        return this.shelfSelected.every((item) => {
          return item.private
        })
      }
    },
    isDownload() {
      if (!this.isSelected) {
        return false
      } else {
        return this.shelfSelected.every((item) => {
          return item.cache
        })
      }
    },
    isSelected() {
      return this.shelfSelected && this.shelfSelected.length > 0
    },
    tabs() {
      return [
        {
          label: this.$t('shelf.private'),
          label2: this.$t('shelf.noPrivate'),
          index: 1,
        },
        {
          label: this.$t('shelf.download'),
          label2: this.$t('shelf.delete'),
          index: 2,
        },
        {
          label: this.$t('shelf.move'),
          index: 3,
        },
        {
          label: this.$t('shelf.remove'),
          index: 4,
        },
      ]
    },
    opcityShow() {
      return this.shelfSelected.length > 0 ? true : false
    },
  },
  methods: {
    chooseItem(item) {
      if (!this.isSelected) {
        return
      }
      switch (item.index) {
        case 1:
          this.showPrivate()
          // console.log('1')
          break
        case 2:
          this.showDownload()
          break
        case 3:
          this.dialog({ groupName: item.title }).show()
          break
        case 4:
          this.showRemove()
          break
        default:
          break
      }
    },
    showPrivate() {
      this.popupMenu = this.popup({
        title: this.isPrivate
          ? this.$t('shelf.closePrivateTitle')
          : this.$t('shelf.setPrivateTitle'),
        btn: [
          {
            text: this.isPrivate
              ? this.$t('shelf.close')
              : this.$t('shelf.open'),
            click: () => {
              this.setPrivate()
            },
          },
          {
            text: this.$t('shelf.cancel'),
            click: () => {
              this.hidePopup()
            },
          },
        ],
      }).show()
    },

    showDownload() {
      this.popupMenu = this.popup({
        title: this.isDownload
          ? this.$t('shelf.removeDownloadTitle')
          : this.$t('shelf.setDownloadTitle'),
        btn: [
          {
            text: this.isDownload
              ? this.$t('shelf.delete')
              : this.$t('shelf.open'),
            click: () => {
              this.setDownload()
            },
          },
          {
            text: this.$t('shelf.cancel'),
            click: () => {
              this.hidePopup()
            },
          },
        ],
      }).show()
    },

    showRemove() {
      let title
      if (this.shelfSelected.length === 1) {
        title = this.$t('shelf.removeBookTitle').replace(
          '$1',
          `《${this.shelfSelected[0].title}》`
        )
      } else {
        title = this.$t('shelf.removeBookTitle').replace(
          '$1',
          this.$t('shelf.selectedBooks')
        )
      }
      this.popupMenu = this.popup({
        title: title,
        btn: [
          {
            text: this.$t('shelf.removeBook'),
            type: 'danger',
            click: () => {
              this.removeSelected()
            },
          },
          {
            text: this.$t('shelf.cancel'),
            click: () => {
              this.hidePopup()
            },
          },
        ],
      }).show()
    },
    removeSelected() {
      this.shelfSelected.forEach((selected) => {
        this.setShelfList(this.shelfList.filter((book) => book !== selected))
      })
      this.hidePopup()
      this.setIsEditMode(false)
      saveBookShelf(this.shelfList)
    },
    async setDownload() {
      this.hidePopup()
      this.setIsEditMode(false)
      saveBookShelf(this.shelfList)
      if (this.isDownload) {
        this.removeSelectedBook()
      } else {
        await this.downloadSelectedBook()
        saveBookShelf(this.shelfList)
        this.easyToast(this.$t('shelf.setDownloadSuccess'))
      }
    },
    async downloadSelectedBook() {
      for (let i = 0; i < this.shelfSelected.length; i++) {
        if (!this.shelfSelected.cache) {
          await this.downloadBook(this.shelfSelected[i]).then((book) => {
            book.cache = true
          })
        }
      }
    },
    removeSelectedBook() {
      Promise.all(this.shelfSelected.map((book) => this.removeBook(book))).then(
        (books) => {
          books.map((book) => {
            book.cache = false
          })
          saveBookShelf(this.shelfList)
          this.easyToast(this.$t('shelf.removeDownloadSuccess'))
        }
      )
    },
    removeBook(book) {
      return new Promise((resolve, reject) => {
        removeLocalStorage(`${book.categoryText}/${book.fileName}-info`)
        removeLocalForage(`${book.fileName}`)
        resolve(book)
      })
    },
    // downloadBook(book) {
    //   return new Promise((resolve, reject) => {
    //     download(book, () => {})
    //   })
    // },
    downloadBook(book) {
      let text = ''
      const toast = this.toast({
        text,
      })
      toast.continueShow()
      return new Promise((resolve, reject) => {
        download(
          book,
          (book) => {
            toast.remove()
            resolve(book)
          },
          reject,
          (progressEvent) => {
            const progress =
              Math.floor((progressEvent.loaded / progressEvent.total) * 100) +
              '%'
            text = this.$t('shelf.progressDownload').replace(
              '$1',
              `${book.fileName}.epub(${progress})`
            )
            toast.updateText(text)
          }
        )
      })
    },
    setPrivate() {
      let isPrivate
      if (this.isPrivate) {
        isPrivate = false
      } else {
        isPrivate = true
      }
      // console.log(this.private)
      this.shelfSelected.forEach((item) => {
        item.private = isPrivate
      })
      // console.log(isPrivate)
      this.finish()

      if (isPrivate) {
        this.easyToast(this.$t('shelf.setPrivateSuccess'))
      } else {
        this.easyToast(this.$t('shelf.closePrivateSuccess'))
      }
    },
    finish() {
      this.hidePopup()
      this.setIsEditMode(false)
      saveBookShelf(this.shelfList)
    },
    hidePopup() {
      this.popupMenu.hide()
    },
    label(item) {
      switch (item.index) {
        case 1:
          return this.isPrivate ? item.label2 : item.label
        case 2:
          return this.isDownload ? item.label2 : item.label
        default:
          return item.label
      }
    },
  },
}
</script>
