  z-index: 150;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: px2rem(42);
  background-color: white;
  box-shadow: 0 px2rem(-2) px2rem(2) 0 rgba(0, 0, 0, 0.1);
  .shelf-footer-tab-wrapper {
    display: flex;
    width: 100%;
    height: 100%;

    .shelf-footer-tab {
      flex: 1;
      font-size: px2rem(14);
      @include center;
      flex-direction: column;
      opacity: 0.5;
      .icon-foot {
        font-size: px2rem(16);
      }
      &:last-child {
        color: violet;
      }
      &.tab-show {
        opacity: 1;
      }
    }
  }
}
</style>
