<!--  -->
<template>
  <transition name="fade">
    <div
      class="shelf-title-wrapper"
      v-show="shelfTitleVisible"
      :class="{ 'shadow-shelf': !shadowShow }"
    >
      <div class="shelf-title">
        <span class="shelf-title-text">{{ shelfTitle }}</span>
        <span class="shelf-select" v-show="isEditMode">{{ selectText }}</span>
      </div>
      <div class="shelf-left" @click="clearCahce" v-show="!ifShowBack">
        <span class="shelf-clearCache">{{ $t('shelf.clearCache') }}</span>
      </div>
      <div class="shelf-left" @click="back" v-show="ifShowBack">
        <span class="icon-back"></span>
      </div>
      <div class="shelf-right" @click="changeEdit">
        <span class="shelf-edit">{{
          isEditMode ? $t('shelf.cancel') : $t('shelf.edit')
        }}</span>
      </div>
    </div>
  </transition>
</template>

<script>
// import { flatten } from '../../utils/book'
import { clearLocalForage } from '../../utils/localForage'
import { clearLocalStorage } from '../../utils/localStorage'
import { storeShelfMixin } from '../../utils/mixin'
export default {
  mixins: [storeShelfMixin],
  props: {
    shelfTitle: String,
    ifShowBack: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      shadowShow: false,
    }
  },
  methods: {
    back() {
      this.$router.go(-1)
    },
    changeEdit() {
      if (!this.isEditMode) {
        this.setShelfSelected([])
        this.setShelfList(
          this.shelfList.map((item) => {
            item.selected = false
            if (item.itemList) {
              item.itemList.forEach((subItem) => {
                subItem.selected = false
              })
            }
            return item
          })
        )
      }
      //每次使用按钮前将所有seleted置为false
      this.setIsEditMode(!this.isEditMode)
    },
    clearCahce() {
      // alert('clear')
      clearLocalStorage()
      clearLocalForage()
      this.setShelfSelected([])
      this.setShelfList([])
      this.getShelfList()
      // console.log('clear')
      this.easyToast(this.$t('shelf.clearCacheSuccess'))
    },
  },
  computed: {
    selectText() {
      const selectNum = this.shelfSelected ? this.shelfSelected.length : 0
      // console.log(selectNum)
      if (selectNum <= 0) {
        return this.$t('shelf.selectBook')
      } else if (selectNum === 1) {
        return this.$t('shelf.haveSelectedBook').replace('$1', selectNum)
      } else {
        return this.$t('shelf.haveSelectedBooks').replace('$1', selectNum)
      }
    },
  },
  watch: {
    offsetY(v) {
      if (v > 0) {
        this.shadowShow = true
      } else {
        this.shadowShow = false
      }
    },
  },
}
</script>
